<h1 id="进入管理员状态下的cmd" tabindex="-1"><a class="header-anchor" href="#进入管理员状态下的cmd" aria-hidden="true">#</a> 进入管理员状态下的cmd</h1>
<p>在你执行<code>npm i vuepress-theme-cli -g</code>命令的时候，需要赋予cmd管理员权限，否则会失败</p>
<ul>
<li>
<p>非管理员状态下的cmd</p>
