</li>
<li>
<p>管理员状态下</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211210200302964" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211210200302964.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
</li>
</ul>
<h2 id="设置" tabindex="-1"><a class="header-anchor" href="#设置" aria-hidden="true">#</a> 设置</h2>
<p>请按照下面这个步骤来，如果你会操作，那就不用按照这个步骤来，此过程以<code>Windows</code>为例，对于比如Linux，MacOs，请自行百度</p>
<ol>
<li>
<p>同时按住<code>Win</code>和<code>X</code>键</p>
<details class="custom-container details"><summary>Win键在哪</summary>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211210201040357" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211210201040357.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
</details>
</li>
<li>
<p>点击<code>Windows 终端(管理员)</code><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211210201202994" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211210201202994.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
</li>
</ol>
